@import '../../scss/variables';
@import '../../scss/mixins';

.container {
  display: flex;
  flex-direction: column;
  margin: 15px;
  position: relative;
  width: 255px;

  &:hover .cardImage img{
    transform: scale(1.1);
  }

  .cardImage {
    display: flex;
    width: 100%;
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
      transition: transform .3s;
    }
  }

  .name {
    margin-top: 16px;
    font: 400 14px 'Spartan', sans-serif;
    color: $darkGrey;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .description {
    @include flexbox-between;
    margin-top: 12px;

    .price {
      font: 700 14px 'Spartan', sans-serif;
    }

    .regularPrice {
      font: 700 12px 'Spartan', sans-serif;
      color: #a0a0a0;
      text-decoration: line-through;
      margin-left: 6px;
    }
  }

  .discount {
    @include flexbox-center;
    position: absolute;
    top: 0;
    left: 0;
    width: 22%;
    height: 8%;
    background-color: #E91E63;
    color: #fff;
    font: 700 12px 'Spartan', sans-serif;
    line-height: 13px;
  }
}
