@import '../../../scss/variables';
@import '../../../scss/mixins';

.wrapper {
  background-image: url(../../../images/main-blocks/special-offer/background.svg);
  height: 580px;

  @include desktop {
    @include flexbox-center;
  }

  .container {
    max-width: $containerWidth;
    margin: auto;

    @include flexbox-center;
    @include tablet {
      width: 100%;
    }
  
    .womanImage {
      position: relative;
      top: 35px;
      z-index: 1;

      @include desktop {
        display: none;
      }
    }
  
    .manImage {
      position: relative;
      top: 25px;
      right: 65px;
      z-index: 1;

      @include desktop {
        display: none;
      }
    }
  
    .subscribe {
      position: relative;
      right: 50px;
      z-index: 0;
      flex-direction: column;
      padding: 90px;
      background-color: #fff;
      border-radius: 5px;
      
      @include flexbox-center;
      @include desktop {
        right: 0;
      }
      @include tablet {
        width: 100%;
        padding: 30px;
      }
  
      .title {
        color: #9C9C9C;
        font: 600 12px 'Spartan', sans-serif;
      }
  
      .text {
        text-align: center;
        color: $mainDark;
        margin-top: 20px;
        font: 600 26px 'Spartan', sans-serif;
  
        .highlighted {
          color: #E91E63;
        }
      }

      form {
        @include flexbox-center;
        flex-direction: column;
        input {
          width: 100%;
          background: #F8F8F8;
          color: #9C9C9C;
          text-align: center;
          border: 0;
          margin-top: 32px;
          padding: 16px 0;
          outline: none;
          font: 500 16px 'Spartan', sans-serif;
        }

        button {
          background-color: $mainDark;
          color: #fff;
          margin-top: 24px;
          padding: 20px 116px;
          font: 500 16px 'Spartan', sans-serif;

          &:disabled {
            opacity: 0.6;
            cursor: not-allowed;
          }
        }

        .subscribeError {
          margin-top: 12px;
          color: red;
        }

        .subscribeSuccess {
          margin-top: 12px;
          color: green;
        }
      }
    }
  }
}
