@import '../../../../scss/variables';
@import '../../../../scss/mixins';

.container {
  font: 400 14px 'Spartan', sans-serif;
  margin: 0 -15px;
  @include flexbox-center;
  @include burger-menu {
    display: none;
  }
  @include tablet {
    flex-direction: column;
    margin: -10 -15px;
  }

  .link {
    margin: 0 15px;
    
    @include tablet {
      display: flex;
      margin: 10px 15px;
    }

    &:hover {
      display: inline;
      border-bottom: 3px solid #f9dd94;
    }
  }
}
